:root{
    --pink-bg: #FFB1AF;
    --pink-accent: #FFD1D1;
    --white: #FFFFFF;
    --yellow-bg: #F9E96E;
}

* {
  box-sizing: border-box;
  margin: 0 !important;
  padding: 0 !important;
}

*:focus {
  outline: none;
}

img {
  width: 100%;
  height: auto;
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  display: block;
  border: none;
}

.container {
  max-width: 1210px;
  width: 100%;
  padding: 0 15px;
  outline: 3px dashed red;
  margin: 0 auto !important;
}

.hero {
  position: relative;
  padding: 261px 0;
}

.hero__img {
  position: absolute;
  height: 100%;
  width: auto;
  left: 0;
  right: 0;
}

.hero__text {
  margin-left: auto !important;
  max-width: 680px;
}

